.contacts__list {
  display: grid;
  grid-template-columns: repeat(2, auto);
  gap: 15px 120px;
  margin: 0;
  padding: 0;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  list-style: none;
  position: relative;
}
.contacts__list::after {
  content: "";
  position: absolute;
  width: 1px;
  height: 100%;
  top: 0;
  left: 57%;
  background-color: #74819c;
}
@media (max-width: 1199px) {
  .contacts__list {
    gap: 24px 96px;
  }
  .contacts__list::after {
    left: 58%;
  }
}
@media (max-width: 767px) {
  .contacts__list {
    grid-template-columns: 1fr;
  }
  .contacts__list::after {
    display: none;
  }
}

.contacts__item-title {
  margin: 0;
  padding: 0 0 5px;
  font-size: 16px;
  font-weight: 700;
}
@media (max-width: 1199px) {
  .contacts__item-title {
    padding: 0 0 10px;
    font-size: 20px;
  }
}

.contacts__info {
  margin: 0;
  font-size: 14px;
  font-weight: 400;
}
@media (max-width: 1199px) {
  .contacts__info {
    font-size: 18px;
  }
}

.footer {
  background-color: #030a1a;
  padding: 34px 0 32px;
  color: #ffffff;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
}
@media (max-width: 1199px) {
  .footer {
    padding: 45px 0 32px;
  }
}
@media (max-width: 1199px) {
  .footer {
    padding: 48px 0 32px;
  }
}

.footer__container {
  display: flex;
  align-items: center;
}
@media (max-width: 767px) {
  .footer__container {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    position: relative;
  }
}

@media (max-width: 1199px) {
  .footer__logo {
    padding: 2px 11px 0 0;
  }
}
@media (max-width: 767px) {
  .footer__logo {
    position: absolute;
  }
}

@media (max-width: 1199px) {
  .footer__logo svg {
    width: 82px;
    height: 18px;
  }
}
@media (max-width: 767px) {
  .footer__logo svg {
    width: 68px;
    height: 15px;
  }
}

.footer__navigation {
  display: flex;
  margin: 0 auto;
}
@media (max-width: 767px) {
  .footer__navigation {
    margin: 0;
  }
}

.footer__navigation-list {
  display: flex;
  gap: 40px;
  list-style: none;
  padding: 0;
  margin: 0;
}
@media (max-width: 1199px) {
  .footer__navigation-list {
    gap: 30px;
  }
}
@media (max-width: 767px) {
  .footer__navigation-list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 15px 75px;
    font-size: 16px;
    margin: 0 0 33px;
  }
}

.footer__navigation-link {
  color: #ffffff;
  line-height: 10px;
}

.footer__navigation-item {
  justify-self: start;
  border-bottom: 1px solid transparent;
}
@media (hover: hover) {
  .footer__navigation-item:hover:not(.focus-visible) {
    border-bottom: 1px solid #ffffff;
  }
}
.footer__navigation-item.focus-visible:focus {
  appearance: none;
}

.footer__navigation-item--mobile {
  display: none;
}
@media (max-width: 767px) {
  .footer__navigation-item--mobile {
    display: block;
  }
}

.footer__social-list {
  list-style: none;
  display: flex;
  gap: 11px;
  padding: 0;
  margin: 0;
}
@media (max-width: 767px) {
  .footer__social-list {
    display: flex;
    justify-content: flex-start;
    align-self: start;
  }
}

.footer__social-item {
  width: 25px;
  height: 25px;
  background-color: #ffffff;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (hover: hover) {
  .footer__social-item:hover:not(.focus-visible) {
    background-color: #103aae;
  }
}
.footer__social-item.focus-visible:focus {
  background-color: #103aae;
}

.footer__social-link {
  display: block;
  width: 18px;
  height: 16px;
  color: #030a1a;
}

.footer__social-link svg {
  display: block;
  width: 100%;
  height: 100%;
}
@media (hover: hover) {
  .footer__social-link svg:hover:not(.focus-visible) {
    color: #ffffff;
  }
}
.footer__social-link svg.focus-visible:focus {
  color: #ffffff;
}

